.header {
  background-image: url('../img/background.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-color: $grey-darken;
  color: $white;
  overflow: hidden;
  position: relative;

  @media (max-width: $min-desktop-width) {
    padding-top: 10px;
    flex-wrap: wrap;
    margin-bottom: 10px;
    background-size: 500% auto;
    background-position: top center;
  }
}

.header__wrap {
  padding-top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 65px;

  &--page {
    margin-bottom: 40px;
  }

  @media (max-width: $min-desktop-width) {
    padding-top: 10px;
    flex-wrap: wrap;
    margin-bottom: 10px;
  }
}

.header__logo {
  display: flex;
  align-items: center;

  @media (max-width: $min-desktop-width) {
    width: 50%;
  }
}

.header__image {
  @media (max-width: $min-desktop-width) {
    width: 90px;
    height: auto;
  }
}

.header__link {
  padding-left: 6px;
  padding-right: 24px;
  line-height: 0;
}

.header__text {
  max-width: 277px;
  font-size: 15px;
  line-height: 21px;
  font-weight: 500;
  margin: 0;
  padding-top: 7px;

  @media (max-width: $desktop-width) {
    font-size: 14px;
  }

  @media (max-width: $min-desktop-width) {
    display: none;
  }
}

.header__contacts {
  display: flex;
  align-items: center;
  position: relative;

  @media (max-width: $min-desktop-width) {
    display: none;
  }
}

.contacts__phone-wrap {
  position: relative;
}

.contacts__button {
  display: block;
  margin-right: 39px;
  cursor: pointer;

  &--phone {
    width: 28px;
    height: 28px;
    padding: 20px 0;
    background-image: url("../img/svg/phone.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }

  &--phone-opened {
    background-image: url("../img/svg/phone-cover.svg");
  }

  &--mail {
    width: 28px;
    height: 28px;
    padding: 20px 0;
    background-image: url("../img/svg/mail.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
}

.contacts-list--mobile {
  display: none;

  @media (max-width: $min-desktop-width) {
    display: block;
    padding-left: 10px;
    list-style: none;
    margin-top: 20px;
    margin-bottom: 20px;
    color: $grey-dark;
    font-size: 14px;
  }
}

.contacts-list-phone {
  z-index: 3;
  position: absolute;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 236px;
  box-sizing: border-box;
  top: 62px;
  left: -102px;
  line-height: 24px;
  font-weight: 500;
  color: $grey-dark;
  background-color: $white;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  padding: 20px 41px;
  display: none;

  &--open {
    display: block;
  }
}

.contacts-list-mail {
  z-index: 3;
  position: absolute;
  list-style: none;
  margin: 0;
  padding: 0;
  width: 273px;
  box-sizing: border-box;
  top: 62px;
  left: -62px;
  line-height: 24px;
  font-weight: 500;
  color: $grey-dark;
  background-color: $white;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
  padding: 20px 41px;
  display: none;

  &--open {
    display: block;
  }
}

.contacts-item {
  padding-bottom: 8px;

  &:last-child {
    padding-bottom: 0;
  }
}

.contacts-link {
  display: block;
  text-decoration: none;
  color: $grey-dark;

  &:hover {
    color: $black;
  }
}

.button__callback {
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: $white;
  border: 2px solid $white;
  padding: 13px 25px;
  font-weight: 500;


  @media (max-width: $desktop-width) {
    padding: 13px 18px;
  }

  &:hover {
    color: $grey-dark;
    background-color: $white;
  }

  &:active {
    color: $grey-dark;
    background-color: $white;
  }
}

.mail-text {
  font-size: 10px;

  &--footer {
    margin: 0;
    color: $grey-light;
  }
}

.header__menu-button {
  display: none;
  width: 40px;
  height: 40px;
  background-color: $transparent;
  cursor: pointer;
  border: none;
  padding: 0;
  background-image: url('../img/svg/menu-close.svg');
  background-repeat: no-repeat;
  background-position: right;
  background-size: auto;

  @media (max-width: $min-desktop-width) {
    display: block;
    width: 50%;
  }

  &--closed {
    background-image: url('../img/svg/menu-open.svg');
    background-repeat: no-repeat;
    background-position: right;
    background-size: auto;
  }
}

body.mouse .contacts__phone-wrap:hover .contacts-list-phone  {
  display: block;
}

body.touch .contacts-list-phone--open  {
  display: block;
}

body.mouse .contacts__button--phone:hover {
  background-image: url("../img/svg/phone-cover.svg");
}

body.mouse .contacts__mail-wrap:hover .contacts-list-mail  {
  display: block;
}

body.touch .contacts-list-mail--open  {
  display: block;
}

